.content {
  height: 100vh;
  width: 300vw;
  background-color: rgb(230, 227, 227);
  display: flex;
  align-items: center;
}

@media (max-width: 600px) {
  .content {
    height: auto;
    width: 100%;
    overflow-x: hidden;
    flex-direction: column;
    align-items: flex-start;
  }
}
