.hamburger {
  display: none;
  cursor: pointer;
  position: absolute;
  top: 1rem;
}

.hamburger span {
  display: block;
  width: 20px;
  height: 2px;
  background-color: rgb(255, 255, 255);
  transition: all 0.3s ease-in-out;
  transform-origin: 0px 1px;
}

.hamburger span:not(:last-child) {
  margin-bottom: 0.35rem;
}

.open span {
  background-color: black;
}

.open span:first-child {
  transform: rotate(45deg);
}

.open span:nth-child(2) {
  opacity: 0;
}

.open span:last-child {
  transform: rotate(-45deg);
}

.mousecursor {
  height: 30px;
  width: 30px;
  background: rgb(93, 93, 226, 0.6);
  border-radius: 50%;
  position: absolute;
  z-index: 99999999;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: all 0.5s ease-in-out;
  transition-property: height, width;
}

.grow {
  height: 50px;
  width: 50px;
}

@media (max-width: 600px) {
  .App {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    overflow-x: hidden;
    cursor: auto;
  }

  .mousecursor {
    display: none;
  }

  .App .body {
    flex: 1 1;
    margin-left: 0;
  }

  .App .sidebar-main {
    position: fixed;
    width: 100vw;
  }

  .hide-sidebar {
    display: none;
    width: 0px;
  }

  .show-sidebar {
    display: block;
    width: 100vw;
    height: 100vh;
  }

  .hamburger {
    display: block;
    z-index: 9999999;
    left: 1rem;
  }
}

.sidebar {
  width: 300px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 2rem;
  background: white;
}

.sidebar_container {
  width: 100%;
}

.sidebar_item {
  text-align: left;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: row;
  transition: all ease-in-out 0.5s;
}

.sidebar_item .left img {
  height: 100%;
  width: 3rem;
  object-fit: contain;
  margin-right: 1rem;
  transition: all ease-in-out 0.5s;
}

.sidebar_item .right h1 {
  position: relative;
  width: 100%;
}

.sidebar_item:hover {
  color: brown;
}

.sidebar_item h1::before {
  content: "";
  height: 2px;
  border-radius: 10%;
  width: 3rem;
  background-color: black;
  position: absolute;
  bottom: 0;
  left: 15%;
  transition: all ease-in-out 0.5s;
}

.sidebar_item h1::after {
  content: "";
  height: 2px;
  border-radius: 10%;
  width: 3rem;
  background-color: black;
  position: absolute;
  bottom: 0;
  left: 15%;
  transition: all ease-in-out 0.5s;
}

.sidebar_item:hover h1::before {
  left: 150%;
  height: 20px;
  width: 5px;
  background-color: rgb(165, 42, 42);
  transform: skew(60deg);
}

.sidebar_item:hover h1::after {
  left: 150%;
  height: 20px;
  width: 5px;
  background-color: rgb(165, 42, 42);
  bottom: -50%;
  transform: skew(-60deg);
}

.sidebar_item:hover img {
  -webkit-filter: invert(26%) sepia(17%) saturate(7000%) hue-rotate(339deg)
    brightness(85%) contrast(91%);
          filter: invert(26%) sepia(17%) saturate(7000%) hue-rotate(339deg)
    brightness(85%) contrast(91%);
}

.sidebar_footer {
  position: absolute;
  left: 25%;
  bottom: 10px;
}

.sidebar_footer span {
  color: rgb(172, 42, 42);
}

@media (max-width: 600px) {
  .sidebar_item {
    margin-bottom: 5rem;
    font-size: 1rem;
  }

  .sidebar {
    width: 100vw;
    padding-left: 25%;
  }

  .sidebar_item:hover h1::after {
    bottom: -33%;
  }

  .sidebar_item:hover h1::after {
    left: 150%;
    height: 20px;
    bottom: -55%;
  }
}

.content {
  height: 100vh;
  width: 300vw;
  background-color: rgb(230, 227, 227);
  display: flex;
  align-items: center;
}

@media (max-width: 600px) {
  .content {
    height: auto;
    width: 100%;
    overflow-x: hidden;
    flex-direction: column;
    align-items: flex-start;
  }
}

.about {
  width: 80vw;
  height: 100vh;
  position: relative;
  overflow-x: hidden;
  position: relative;
  overflow-y: hidden;
}

.background {
  position: absolute;
  height: 100%;
  width: 100%;
}

.about .top {
  position: absolute;
  top: 0;
  width: 100%;
  height: 25px;
  display: grid;
  place-items: center;
  color: white;
  background-color: rgb(61, 61, 189, 0.8);
}

.info {
  position: absolute;
  top: 5rem;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  padding-left: 10vw;
  overflow-y: hidden;
}

.info img {
  height: 40vh;
  width: 40vh;
  border-radius: 50%;
  object-fit: contain;
}

.info .intro {
  margin-top: 1rem;
  text-align: center;
}

.info .right {
  padding-left: 5vw;
  padding-right: 20vw;
}

.info .right .myInfo {
  margin-top: 2rem;
  text-align: justify;
}

.right .quote {
  font-family: "Indie Flower", cursive;
  font-size: 2rem;
}

.background-image img {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
  opacity: 0.7;
  transform: rotate(180deg);
}

@media (max-width: 1000px) {
  .info {
    padding-left: 10px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .info .left {
    margin-right: 6rem;
  }
}

@media (max-width: 600px) {
  .about {
    height: 100vh;
    width: 100vw;
  }

  .about .top {
    height: 50px;
    position: fixed;
    background-color: rgb(61, 61, 189);
    z-index: 999999;
  }

  .info img,
  .info .left,
  .info .right {
    padding: 0;
  }

  .info {
    padding: 0 1rem;
  }

  .info .right .quote {
    text-align: center;
    margin-top: 1rem;
  }

  .background-image img {
    opacity: 0.3;
  }

  .info .left {
    margin-right: 0;
  }
}

.contact {
  width: 100vw;
  height: 100%;
  background-color: rgb(60, 114, 121);
}

@media (max-width: 600px) {
  .contact {
    height: 1000px;
  }
}

.work {
  width: 100vw;
  height: 100%;
  background-color: rgb(205, 99, 50);
}

@media (max-width: 600px) {
  .work {
    height: 1000px;
  }
}

